import React from "react";

import {
  Button,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Container,
  Row,
  Col
} from "reactstrap";

import postcard from "../assets/img/postcard.jpg";

class Contact extends React.Component{
	constructor(){
		super();
		this.state = {
			name: '',
			email: '',
			message: ''
		};
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
	}

	handleChange(event) {
		let nam = event.target.name;
    let val = event.target.value;
    this.setState({[nam]: val});
	}

  handleSubmit = (event) => {
		fetch('https://localhost:3000/api/contact', {
			method: 'POST',
			headers: {'Content-Type': 'application/json'},
			body: JSON.stringify(this.state)
    })
		.then(function(response) {
      console.log(response)
      return response.json();
    });
  event.preventDefault();
  }

  componentDidUpdate = () => {
    console.log(this.state)
  }

  render(){
  return (
        <div className="section section-contact-us text-center">
          <Container>
            <h2 className="title">Want to work with us?</h2>
            <p className="description">We'd love to hear from you!</p>
            <Row>
              <Col className="text-center ml-auto mr-auto" lg="6" md="8">
                <Container className="image-container" style={{paddingBottom: "1rem"}}>
                  <img src={postcard} alt="Train Terminal" />
                </Container>
              </Col>
              <Col className="text-center ml-auto mr-auto" lg="6" md="8">
              <form onSubmit={this.handleSubmit}>
                <InputGroup
                  className={
                    "input-lg"
                  }
                >
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="now-ui-icons users_circle-08"></i>
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Name..."
                    required
                    type="text"
                    name="name"
                    onChange={this.handleChange}
                    value={this.state.name}
                  ></Input>
                </InputGroup>
                <InputGroup
                  className={
                    "input-lg"
                  }
                >
                  <InputGroupAddon addonType="prepend">
                    <InputGroupText>
                      <i className="now-ui-icons ui-1_email-85"></i>
                    </InputGroupText>
                  </InputGroupAddon>
                  <Input
                    placeholder="Email..."
                    type="text"
                    name="email"
                    onChange={this.handleChange}
                    value={this.state.email}
                  ></Input>
                </InputGroup>
                <div className="textarea-container">
                  <Input
                    cols="80"
                    placeholder="Type a message..."
                    rows="4"
                    type="textarea"
                    name="message"
                    onChange={this.handleChange}
                    value={this.state.message}
                  ></Input>
                </div>
                <div className="send-button">
                  <Button
                    block
                    className="btn-round"
                    color="info"
                    size="lg"
                    type="submit"
                    value="submit"
                  >
                    Send Message
                  </Button>
                </div>
              </form>
              </Col>
            </Row>
          </Container>
        </div>
      );
  }
}

export default Contact;